body {user-select:none; overflow:hidden;}

body.website {overflow:auto;}

div, ul, li, p, span, h1, h2, h4, a, fieldset, legend, iframe {margin:0; padding:0;}

li {list-style:none;}

#Link {text-decoration:none}

@keyframes screenOpen {
    0% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, 100vh);
        width:200px;
        height:200px;
        border-radius:50%;
        overflow:hidden;
    }
    50% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
        width:200px;
        height:200px;
        border-radius:50%;
        overflow:hidden;
    }
    100% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
        width:100%;
        height:100%;
        border-radius:unset;
        overflow:hidden;
    }
}

@keyframes screenChange {
    from {
        overflow:hidden;
        opacity:1;
        visibility:visible;
        transform:translateX(0);
    }
    to {
        overflow:hidden;
        opacity:0;
        visibility:hidden;
        transform:translateX(-100px);
    }
}