.skills {
    display:flex;
    justify-content:center;
    align-items:center;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    background:rgb(29, 29, 29);
    overflow-y:auto !important;
    height:100vh;
}

.skills .sWrap {
    width:70vw;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease-out;
}

.skills .sWrap a {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:25px;
    color:rgb(172, 172, 172);
    text-decoration:none;
    transition:0.2s color;
}

.skills .sWrap a:hover {
    color:white;
}

.skills .sWrap a span {
    margin-left:10px;
}

.skills .sWrap h1 {
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:50px;
    color:white;
    height:100px;
}

.skills .content {
    padding-top:50px;
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    border:2px solid white;
    border-radius:10px;
    height:500px;
    box-sizing:border-box;
}

.skills .content div {
    justify-self:center;
    text-align:center;
}

.skills .content h4 {
    padding:5px 15px 5px 15px;
    font-size:25px;
    border-radius:20px;
    background:white; 
    margin-bottom:40px;
}

.skills .content ul {
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-column-gap:20px;
    grid-row-gap:20px;
}

.skills .content ul li {
    position:relative;
    width:100px;
    height:100px;
    overflow:hidden;
}

.skills .content ul li img {
    width:inherit;
    height:inherit;
    animation-duration:0.2s;
    animation-fill-mode:both;
}

.skills .content ul li span {
    position:absolute;
    top:0;
    left:0;
    display:flex;
    justify-content:center;
    align-items:center;
    width:inherit;
    height:inherit;
    color:white;
    transform:rotateY(90deg);
    animation-duration:0.2s;
    animation-fill-mode:both;
    background:rgba(0,0,0,0.3);
}

@keyframes hoverAnim {
    from {
        transform:rotateY(0deg);
    }
    to {
        display:none;
        transform:rotateY(90deg);
    }
}

@keyframes notHoverAnim {
    from {
        display:none;
        transform:rotateY(90deg);
    }
    to {
        transform:rotateY(0deg);
    }
}

.typescript {padding-top:10px; width:90px !important; height:90px !important;}
.mariadb {padding-top:20px;}
.mysql {padding-top:15px; width:80px !important; height:70px !important}

@media (max-width:1500px) {
    .skills .sWrap {
        width:1050px;
    }

    .skills .content h4 {
        width:190px;
    }
}