.other {
    display:flex;
    justify-content:center;
    align-items:center;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    background:rgb(29, 29, 29);
    overflow-y:auto !important;
    height:100vh;
}

.other .otherWrap {
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease-out;
}

.other .otherWrap a {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:25px;
    color:rgb(172, 172, 172);
    text-decoration:none;
    transition:0.2s color;
}

.other .otherWrap a:hover {
    color:white;
}

.other .otherWrap a span {
    margin-left:10px;
}

.other .otherWrap fieldset {
    margin-top:50px;
    border:5px solid #727272;
    border-radius:20px;
    box-sizing:border-box;
}

.other .otherWrap fieldset legend {
    font-size:30px;
    text-align:center;
    font-weight:bold;
    color:white;
}

.other .content {
    display:flex;
    justify-content:center;
    margin-top:50px;
    text-align:left;
}

.other .content p {
    padding:10px 30px 10px 30px;
    font-size:20px;
    line-height:50px;
    white-space:pre-line;
    width:max-content;
    overflow:hidden;
    color:rgb(255, 255, 255);
}