/* first css */
.first {
    height:100vh;
}

.first .fw {
    height:100%;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    background:rgb(29, 29, 29);
}

.first .fs {
    display:flex;
    flex-direction:column;
    height:100%;
}

@keyframes screenChange {
    0% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
        width:100%;
        height:100%;
        border-radius:unset;
        overflow:hidden;
    }
    50% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
        width:200px;
        height:200px;
        border-radius:50%;
        overflow:hidden;
    }
    100% {
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, 100vh);
        width:200px;
        height:200px;
        border-radius:50%;
        overflow:hidden;
    }
}

    /* Header css */
.first .header {
    display:flex;
    justify-content:right;
    align-items:center;
    width:100%;
    height:200px;
}

.first .headerWrap {
    display:flex;
    justify-content:space-between;
    width:95vw;
}

.first .header .portfolio {
    position:relative;
    width:600px;
    display:flex;
    align-items:center;
    font-size:40px;
    font-weight:500;
    color:white;
    overflow:hidden;
}

.first .header .portfolio h2 {
    opacity:0;
    visibility:hidden;
    animation-name:portfolioAnim;
    animation-duration:1.5s;
    animation-fill-mode:both;
    animation-timing-function:ease;
}

.first .header .portfolio:hover {
    cursor:default;
}

@keyframes portfolioAnim {
    0% {
        transform:translateY(-100px);
    }
    100% {
        opacity:1;
        visibility:visible;
        transform:translateY(0);
    }
}

.first .header .menu {
    flex:1;
    height:200px;
    display:flex;
    justify-content:space-between;
    overflow:hidden;
}

.first .header .menuWrap {
    opacity:0;
    visibility:hidden;
    flex:1;
    padding-right:30px;
    display:flex;
    justify-content:center;
    align-items:center;
    animation-name:menuAnim;
    animation-duration:2s;
    animation-fill-mode:both;
    animation-delay:1s;
}

@keyframes menuAnim {
    0% {
        transform:translateX(150px);
    }
    100% {
        opacity:1;
        visibility:visible;
        transform:translateX(0);
    }
}

.first .header .menuWrap a:hover {
    cursor:pointer;
}

.first .header .menuWrap ul {
    display:flex;
    flex-direction:column;
    width: 200px;
    height:140px;
    color:white;
}

.first .header .menuTit {
    position:relative;
    font-size:35px;
    overflow:hidden;
    height:60px;
    color:white;
}

.first .header .menuLine {
    left:-200px;
    background:linear-gradient(to bottom, white, white);
    transition:0.5s;
    z-index:1;
}

.first .header .menuLine.line {
    left:0;
}

.first .header .menuTit::after {
    background:linear-gradient(to bottom, transparent 30%, rgb(145, 144, 144));
}

.first .menuLine,
.first .header .menuTit::after {
    position:absolute;
    content:"";
    display:block;
    bottom:0;
    width:100%;
    height:2px;
}

.first .header .menuCtn {
    flex:1;
    margin-top:10px;
    display:flex;
    align-items:center;
    font-size:14px;
    white-space:pre-line;
}
    /* //Header css */

    /* Section css */
.first .section {
    display:flex;
    justify-content:right;
    align-items:center;
    height:80%;
}

.first .section .introduce {
    display:flex;
    flex-direction:column;
    width:95vw;
    height:350px;
    font-size:45px;
    white-space:nowrap;
    color:white;
}

.first .section .introduce h1 {
    display:flex;
    align-items:center;
    width:fit-content;
    box-sizing:border-box;
    animation-duration:1s;
    animation-iteration-count:infinite;
}

.first .section .fIntro {
    width:fit-content;
}

.first .section .sIntro {
    margin-top:50px;
    margin-left:300px;
}

.first .section .sIntro.introAnim {
    animation-name: cursorAnim;
}

@keyframes cursorAnim {
    from {
        border:none;
    }
    to {
        border-right:3px solid white;
    }
}
    /* //Section css */

    /* Footer css */
.first .footer {
    display:flex;
    justify-content:center;
}

.first .footerWrap {
    display:flex;
    justify-content:center;
    width:95vw;
    font-size:50px;
    color:white;
    animation-name:footerWrapAnim;
    animation-duration:1.5s;
    animation-fill-mode:both;
    animation-delay:1.8s;
    animation-timing-function:ease;
    opacity:0;
    margin-bottom:20px;
    visibility:hidden;
}

@keyframes footerWrapAnim {
    from {
        transform:translateY(30px);
    }
    to {
        opacity:1;
        visibility:visible;
        transform:translateY(0);
    }
}

.first .footerWrap div {
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
}

.first .footer .downArrow:hover {
    cursor:pointer;
}

.first .footerWrap span {
    position:absolute;
    right:-50px;
    font-size:15px;
}

.first .footerWrap span:hover {
    cursor:default;
}
    /* //Footer css */
/* //first css */

/* second css */
    /* Header css */
.second {
    height:100vh;
}

.second .header {
    display:flex;
    justify-content:center;
}

.second .headerWrap {
    display:flex;
    justify-content:center;
    width:95vw;
    color:black;
    opacity:0;
    visibility:hidden;
    animation-duration:0.5s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    animation-delay:1s;
}

@keyframes hwAnim {
    from {
        transform:translateY(-30px);
    }
    to {
        opacity:1;
        visibility:visible;
        transform:translateY(0);
    }
}

.second .headerWrap div {
    margin-top:25px;
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
}

.second .headerWrap .downArrow {
    font-size:50px;
}

.second .headerWrap .downArrow:hover {
    cursor:pointer;
}

.second .headerWrap span {
    position:absolute;
    right:-40px;
    font-size:15px;
}
    /* //Header */

    /* Section */
.second .section {
    width:100vw;
    height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
}

.second .sectionWrap {
    margin:0 auto;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    width:95vw;
    height:800px;
}

.second .sectionWrap h1 {
    display:flex;
    justify-content:center;
    align-items:center;
    width:70%;
    height:100px;
    font-size:50px;
    border:2px solid #111;
    border-top-left-radius:30px;
    border-top-right-radius:30px;
    border-bottom:none;
    box-sizing:border-box;
    background:rgba(173, 236, 173, 0.61);
}

.second .sectionWrap .content {
    display:flex;
    width:70%;
    height:500px;
    border:2px solid #111;
    border-bottom-left-radius:30px;
    border-bottom-right-radius:30px;
    box-sizing:border-box;
    background:rgba(255, 255, 255, 0.61)
}

.second .sectionWrap .left {
    flex:1;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    opacity:0;
    visibility:hidden;
    animation-duration:0.5s;
    animation-fill-mode:both;
    animation-timing-function:ease;
}

@keyframes leftAnim {
    from {
        transform:translateX(-100px);
    }
    to {
        opacity:1;
        visibility:visible;
        transform:translateX(0);
    }
}

.second .sectionWrap .left div {
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-column-gap:130px;
    grid-row-gap:70px;
    width:85%;
}

.second .sectionWrap .left ul {
    display:flex;
    width:200px;
    align-items:center;
    white-space:nowrap;
}

.second .sectionWrap .left ul li:first-child {
    font-size:25px;
    font-weight:bold;
    margin-right:30px;
}

.second .sectionWrap .right {
    flex:0.5;
    display:flex;
    align-items:center;
    opacity:0;
    visibility:hidden;
    animation-duration:0.5s;
    animation-delay:0.5s;
    animation-fill-mode:both;
    animation-timing-function:ease;
}

@keyframes rightAnim {
    from {
        transform:translateX(100px);
    }
    to {
        opacity:1;
        visibility:visible;
        transform:translateX(0);
    }
}

.second .sectionWrap .right div {
    width:300px;
    height:400px;
}

.second .sectionWrap .right img {
    width:inherit;
    height:inherit;
}
    /* //Section */
@media (min-width:1600px) {
    .second .sectionWrap h1,
    .second .sectionWrap .content {
        width:1035px;
    }
}

@media (max-width:1550px) {
    .first,
    .second {
        overflow:hidden;
    }
    .second .sectionWrap h1,
    .second .sectionWrap .content {
        width:1035px;
    }
}

@media (max-height:800px) {
    .second {
        height:max-content;
    }
}

@media (max-height:650px) {
    .first {
        height:max-content;
    }
}

@media (max-height:500px) {
    .first .section {
        height:max-content;
    }
}