.website {
    display:flex;
    justify-content:center;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    background:rgb(29, 29, 29);
}

.website .ww {
    margin-top:50px;
    width:1338px;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease-out;
}

.website .ww a {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:25px;
    color:rgb(172, 172, 172);
    text-decoration:none;
    transition:0.2s color;
}

.website .ww a:hover {
    color:white;
}

.website .ww a span {
    margin-left:10px;
}

.website .content {
    margin-top:200px;
    display:flex;
    flex-direction:column-reverse;
    width:100%;
}

.website .contentParent {
    perspective: 1000px;
}

.website .contentSubWrap {
    padding:10px;
    display:flex;
    width:1000px;
    height:300px;
    border-width:2px;
    border-style:solid;
    border-radius:20px;
    margin-bottom:100px;
    animation-duration:0.5s;
    animation-fill-mode:both;
}

.website .contentParent:nth-child(odd) .contentSubWrap {
    justify-self:left;
}

.website .contentParent:nth-child(even) .contentSubWrap {
    justify-self:right;
}

@keyframes appearAnim {
    0% {
        transform-origin:left;
        transform:rotate3d(0, 1, 0, 90deg);    
    }
    100% {
        transform-origin:left;
        transform:rotate3d(0, 1, 0, 0deg);
    }
}

@keyframes appearReverseAnim {
    0% {
        transform-origin:right;
        transform:rotate3d(0, 1, 0, -90deg);    
    }
    100% {
        transform-origin:right;
        transform:rotate3d(0, 1, 0, 0deg);
    }
}

.website .contentSubWrap .title {
    font-size:30px;
    font-weight:bold;
    color:white;
    padding-inline:20px;
}

.website .contentParent:nth-child(odd) .title {
    text-align:left;
    margin-left:80px;
}

.website .contentParent:nth-child(even) .title {
    text-align:right;
    margin-right:80px;
}

.website .insideWrap  {
    display:flex;
    width:1000px;
    height:100%;
}

.website .contentParent:nth-child(odd) .insideWrap {
    flex-direction:row;
}

.website .contentParent:nth-child(even) .insideWrap {
    flex-direction:row-reverse;
}

.website .iframe {
    display:flex;
    width:350px;
    background:white;
    border:1px solid white;
    border-radius:10px;
    box-sizing:border-box;
    overflow:hidden;
}

.website .iframe img {
    width:100%;
    border-radius:10px;
}

.website .insideMid {
    flex:1;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    align-items:center;
    height:inherit;
}

.website .contentParent:nth-child(odd) .insideMid {
    margin-left:20px;
}

.website .contentParent:nth-child(even) .insideMid {
    margin-right:20px;
}

.website .contentInside p {
    align-self:normal;
    font-size:14px;
    color:white;
    text-align:center;
    line-height:25px;
}

.website .contentParent:nth-child(odd) .contentInside p {
    text-align:left;
}

.website .contentParent:nth-child(even) .contentInside p {
    text-align:right;
}

.website .contentInside ul {
    display:flex;
    flex-direction:row;
    gap:10px;
}

.website .contentInside ul li {
    width:35px;
    height:35px;
    border-radius:30%;
    border:1px solid #111;
    box-sizing:border-box;
}

.website .contentInside a {
    padding-block:5px;
    padding-inline:10px;
    border:2px solid rgb(184, 184, 184);
    border-radius:0px;
    transition:0.2s;
    box-sizing:border-box;
    font-weight:bold;
}

.website .contentInside fieldset {
    padding:5px;
    align-self:normal;
    width:inherit;
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    grid-row-gap:10px;
    border:none;
}

.website .contentInside fieldset div {
    display:flex;
}

.website .contentInside fieldset legend {
    text-align:center;
    font-weight:bold;
    color:gray;
}

.website .contentInside fieldset span:first-child {
    margin:0 auto;
    font-weight:bold;
}

.website .contentInside fieldset div:last-child span:last-child {
    display:none;
}

.react {color:#63d1f5;}
.typescript {color:#2f74c0;}
.javascript {color:#efd81d;}
.express {color:#353535;}
.mariadb {color:#0b2869;}
.react-query {color:#00425a;}

.gcp {
    background:linear-gradient(to right, #4183f1 40%, #e40200 40%, #f7b303);
    background-clip:content-box;
    -webkit-background-clip:text;
    color:transparent;
}
.docker {color:#0396c0;}
